import React, { Component } from "react";
import axios from "axios";
import { Link } from "react-router-dom";

import Spinner from "./Spinner";
export default class Books extends Component {
  state = {
    error: null,
    loading: false,
    books: [],
    pagination: [],
  };

  componentDidMount = () => {
    this.setState({ loading: true });
    axios
      /*
		TODO:
		1. config env файлаас уншдаг болгох
		*/
      .get("http://localhost:8080/api/v1/books?limit=10")
      .then((result) => {
        this.setState({
          books: result.data.data,
          pagination: result.data.pagination,
          loading: false,
        });
      })
      .catch((err) => this.setState({ error: err.response, loading: false }));
  };

  statelessComp = () => {
    let arr = Array.apply(null, {
      length: this.state.pagination.pageCount,
    }).map(Number.call, Number);
    return (
      <ul className="pagination-list">
        {arr.map((item) => {
          /* TODO:
				1. Pagination шилждэг болгох
				2. Active Pagination гаргаж ирэх 
				*/

          return (
            <li>
              <a
                className="pagination-link is-current"
                aria-label="Page"
                aria-current="page"
              >
                {item + 1}
              </a>
            </li>
          );
        })}
      </ul>
    );
  };

  render() {
    return (
      <div>
        <div className="columns is-vcentered">
          <div className="column is-8">
            <h1 className="is-primary is-size-2">Номын сан</h1>
          </div>
          <div className="column">
            <h1 className="is-primary is-size-4">Библи номын цоморлиг</h1>
          </div>
        </div>
        {this.state.loading ? (
          <Spinner />
        ) : (
          <div className="container">
            <div className="columns is-multiline">
              {this.state.books.map((el) => (
                <div className="column is-3">
                  <Link to={`books/${el._id}`}>
                    <img
                      style={{ width: "50%" }}
                      style={{ height: "25vh" }}
                      src={`https://i.postimg.cc/` + el.photo}
                    />
                  </Link>
                  <p className="mt-4">{el.name}</p>
                </div>
              ))}
            </div>
          </div>
        )}

        <div className="columns is-mobile is-centered my-6">
          <nav className="pagination" role="navigation" aria-label="pagination">
            {this.state.pagination.start != 1 && (
              <a
                className="pagination-previous"
                title="This is the first page"
                disabled
              >
                Өмнөх хуудас
              </a>
            )}

            {!this.state.pagination.nextPage ||
              (this.state.pagination.pageCount != 1 && (
                <a className="pagination-next">Дараагийн хуудас</a>
              ))}
            {this.statelessComp()}
          </nav>
        </div>
        <div className="columns is-mobile is-centered my-6"></div>
        <footer class="footer">
          <div class="content has-text-centered">
            <p>Хэрэв та манай үйл ажиллагааг дэмжиж байвал хандив өргөөрэй</p>
          </div>
        </footer>
      </div>
    );
  }
}
