import React, { Component } from "react";
import axios from "axios";

export default class BookDetail extends Component {
	state = {
		name: null,
		content: null,
		photo: null,
		error: null,
		success: null,
		deleted: null,
		loading: null,
		loading: false,
	};

	goBack = () => {
		this.props.history.goBack();
	};

	handleSave = () => {
		const token = localStorage.getItem("token");
		this.setState({ loading: true, success: null });
		axios
			.post(
				"http://localhost:8080/api/v1/books/",
				{
					name: this.state.name,
					content: this.state.content,
					photo: this.state.photo,
				},
				{
					headers: { Authorization: `Bearer ${token}` },
				},
			)
			.then((result) => {
				console.log(result);

				this.setState({
					...result.data.data,
					error: null,
					loading: false,
					success: "Амжилттай хадгагагдлаа...",
				});
			})
			.catch((err) => {
				this.setState({
					error: err.response.data.error.message,
					loading: false,
				});
			});
	};



	handleType = (e) => {
		const { name, value } = e.target;
		this.setState({ [name]: value, error: null, success: null });
	};

	componentDidMount = () => {
		axios
			.get("http://localhost:8080/api/v1/books/" + this.props.match.params.id)
			.then((result) =>{
				this.setState({ ...result.data.data, error: null, loading: false });
				console.log(result.data.data);
			}
			
			)
			.catch((err) => {
				this.setState({
					error: err.response.data.error.message,
					loading: false,
				});
			});
	};
	
	render() {
		if (this.state.deleted) {
			return <div className="notification is-danger">Ном устгагдлаа</div>;
		}
		return (
			<>
				<div className="media">
					<div className="media-left">
						<img
							src={`https://i.postimg.cc/` + this.state.photo}
						/>
					</div>
					<div className="media-content">
						<h1 className="title">{this.state.name}</h1>
						<div className="field">
							<label className="label">Нэр</label>
							<input
								className="input"
								name="name"
								value={this.state.name}
								onChange={this.handleType}
							></input>
							<label className="label">Зураг</label>

							<input
								className="input"
								name="photo"
								value={this.state.photo}
								onChange={this.handleType}
							></input>

							<label className="label">Агуулга</label>
							<textarea
								style={{ height: "20em" }}
								className="input"
								name="content"
								value={this.state.content}
								onChange={this.handleType}
							></textarea>
							<button className="button is-link is-outlined m-2" onClick={this.handleSave}>
								Ном оруулах
							</button>
							<button className="button is-success is-outlined m-2" onClick={this.goBack}>
								Цуцлах
							</button>
							
							{this.state.success && (
								<div className="notification is-success">
									{this.state.success}
								</div>
							)}
							{this.state.error && (
								<div className="notification is-warning">
									{this.state.error}
								</div>
							)}
						</div>
					</div>
				</div>
			</>
		);
	}
}
